@import 'src/styles/colors.scss';
@import 'src/styles/fonts.scss';

.select {
  position: relative;
  display: flex;
  padding: 15px;
  justify-content: space-between;
  align-items: center;
  background-color: $bg_color;
  width: 100%;
  height: 52px;
  border-radius: 12px;
  border: 1px solid transparent;

  &_open {
    border: 1px solid $outline
  }

  &__input {
    @include text($text_color, $regular);
  }

  &__control {
    display: flex;
    min-width: 20px;
    min-height: 20px;
    background-image: url(../../images/Arrow/chevron_down_20.svg);
    background-size: contain;
  }

  &__menu {
    position: absolute;
    left: 0;
    top: 62px;
    display: flex;
    flex-direction: column;
    padding: 0 16px;
    background-color: $bg_color;
    width: 100%;
    border-radius: 12px;

    &_hidden {
      display: none;
    }
  }

  &__option {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 48px;
    border-bottom: 1px solid #FFFFFF0D;
    @include text($text_color, $regular);

    &:last-of-type {
      border: none;
    }

    &-checked {
      display: flex;
      min-width: 20px;
      min-height: 20px;
      mask-image: url(../../images/Interface/Check.svg);
      background-color: $accent_text_color;
      mask-size: cover;
    }
  }
}

.option {
  display: flex;
  align-items: center;

  &__img {
    width: 24px;
    height: 24px;
    border-radius: 8px;
    margin-right: 8px;
  }

  &__title {
    @include text($text_color, $regular);
    margin-right: 4px;
  }

  &__caption {
    @include text($hint_color, $regular);
  }
}