@import 'src/styles/colors.scss';
@import 'src/styles/fonts.scss';
    
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.withdraw {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__input {
    outline: none;
    border: none;
    background-color: transparent;
    @include text($text_color, $regular);
    width: 100%;

    &::placeholder {
      @include text($secondary_hint_color, $regular);
    }

    &_error {
      color: $destructive_text_color;
    }

    &-caption {
      margin: 16px 0 6px;
      text-transform: uppercase;
      @include caption_1($section_header_text_color, $regular);

      &:last-of-type {
        margin-top: 10px;
      }
    }

    &-box {
      display: flex;
      gap: 8px;
      padding: 15px;
      align-items: center;
      background-color: $bg_color;
      width: 100%;
      height: 52px;
      border-radius: 12px;
      border: 1px solid transparent;
    
      &:focus-within {
        border: 1px solid $outline
      }

      &_error {
        border: 1px solid $desctuctive_bg;
      }
    }

    &-suffix {
      @include text($text_color, $regular);
    }

    &-max-btn {
      padding: 0;
      font-size: 17px;
    }
  }

  &__coin {
    &-box {
      display: flex;
      gap: 8px;
      padding: 16px;
      align-items: center;
      background-color: $bg_color;
      width: 100%;
      height: 52px;
      border-radius: 12px;
    }

    &-icon {
      width: 24px;
      height: 24px
    }

    &-name {
      @include text($text_color, $regular);
      text-transform: uppercase
    }

    &-net {
      @include text($hint_color, $regular);
      margin-left: -4px;
    }
  }

  &__address-input {
    display: flex;
    padding: 15px;
    background-color: $bg_color;
    width: 100%;
    height: 84px;
    min-height: 84px;
    border-radius: 12px;
    border: 1px solid transparent;
    outline: none;
    @include text($text_color, $regular);
    resize: none;
  
    &:focus {
      border: 1px solid $outline
    }

    &::placeholder {
      @include text($secondary_hint_color, $regular);
    }

    &_error {
      border: 1px solid $desctuctive_bg;
      color: $destructive_text_color;
    }
  }

  &__error {
    margin: 4px 0;
    @include caption_1($destructive_text_color, $regular);
    text-transform: uppercase;
  }

  &__balance {
    @include caption_1($link_color, $regular);
    margin-right: 4px;

    &-box {
      display: flex;
      align-items: center;
      margin-top: 2px;
    }

    &-caption {
      @include caption_1($hint_color, $regular);
      text-transform: uppercase;
      margin-right: 10px;
    }

    &-coin-icon {
      width: 22px;
      height: 22px;
    }
  }

  &__receive {
    &-box {
      margin-top: auto;
      display: flex;
      flex-direction: column;
      gap: 8px;
    }

    &-caption {
      display: flex;
      align-items: center;
      gap: 10px;

      &-icon {
        background-color: $bg_color;
        border-radius: 4px;
        padding: 3px;
        width: 24px;
        height: 24px;
      }

      &-text {
        @include title_3($text_color, $regular)
      }
    }

    &-amount {
      font-family: 'San Francisco Rounded';
      font-size: 48px;
      line-height: 1;
      color: $text_color;

      &-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    &-currency {
      font-family: 'San Francisco Rounded';
      font-size: 24px;
      line-height: 1;
      color: $hint_color;
    }
  }

  &__send-btn {
    width: 100%;
    margin: 24px 0 0;
  }
}