@import 'src/styles/fonts.scss';
@import 'src/styles/colors.scss';

.success-withdraw {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 24px;
  justify-content: center;
  align-items: center;

  &__img {
    width: 128px;
    height: 128px;
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: center;
    max-width: 220px;
  }

  &__title {
    @include title_3($text_color, $semibold)
  }

  &__caption {
    @include text($hint_color, $regular);
    text-align: center;
  }
}