@import 'src/styles/fonts.scss';
@import 'src/styles/colors.scss';

.list-item {
  display: flex;
  align-items: center;
  gap: 12px;
  
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 48px;
    min-height: 48px;
    background-color: $skeleton;
    border-radius: 4px;
    font-size: 28px;
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 2px;
    width: 100%;
  }

  &__title {
    @include subheadline($text_color, $semibold);
    display: flex;
    align-items: center;
    gap: 14px;
  }

  &__subtitle {
    @include caption_1($hint_color, $regular);
  }

  &__button {
    display: flex;
    min-width: 24px;
    height: 24px;
    background-color: transparent;
    border: none;
    mask-image: url(../../../images/Arrow/Chevron_Left.svg);
    background-color: $hint_color;
    rotate: 180deg;
  }
}