@import './styles/colors.scss';

.app {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  transition: height .2s ease-out;

  &_ios {
    height: calc(100vh - 34px);
    transition: none;
  }

  &_on-keyboard {
    height: var(--tg-viewport-stable-height);
    transition: none;
  }
}

.app_on-keyboard.app_ios {
  height: calc(var(--tg-viewport-stable-height) + 50px);
  transition: none;
}

.content {
  display: flex;
  height: calc(100% - 51px);
  overflow: scroll;
  background-color: $black;
}
