@import 'src/styles/fonts.scss';
@import 'src/styles/colors.scss';

.balance {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 8px;

  &__caption {
    @include subheadline($text_color, $semibold)
  }

  &__box {
    display: flex;
    align-items: center;
    gap: 4px;
  }

  &__coins {
    font-family: 'San Francisco Rounded';
    font-size: 44px;
    line-height: 53px;
    color: $text_color;

    &_hint {
      font-family: 'San Francisco Rounded';
      color: $hint_color;
    }
  }

  &__coin-icon {
    display: flex;
    width: 40px;
    height: 40px;
    background-image: url(../../../images/Coin.png);
    background-size: contain;
  }

  &__usd {
    @include subheadline($hint_color, $regular)
  }

  &__actions {
    display: flex;
    padding: 4px 8px;
    border-radius: 12px;
    background-color: $bg_color;
  
    &-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 6px;
      width: 100%;
    }
  }
  
  &__transactions {
    display: flex;
    flex-direction: column;
    flex-direction: 12px;
    gap: 12px;
  
    &-caption {
      @include headline($text_color, $semibold)
    }

    &-placeholder {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 12px;
      border-radius: 16px;
      background-color: $bg_color;
      width: 100%;
      height: 223px;

      &-icon {
        font-size: 64px;
      }

      &-text {
        @include text($hint_color, $regular);
        max-width: 169px;
        text-align: center;
      }
    }
  }

  &__transaction-text {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__bottom-pd {
    display: flex;
    min-height: 8px;
  }
}