@import 'src/styles/fonts.scss';
@import 'src/styles/colors.scss';

.wallet {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
  width: 100%;
  height: 100%;

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    @include large_title($text_color, $semibold)
  }

  &__profile {
    display: flex;
    align-items: center;
    gap: 8px;

    &-avatar {
      border-radius: 50%;
      width: 36px;
      height: 36px;
    }

    &-text {
      display: flex;
      flex-direction: column;
      gap: 2px;
    }

    &-name {
      @include caption_1($text_color, $semibold)
    }

    &-reg {
      @include caption_2($hint_color, $regular)
    }
  }

  &__settings-btn {
    background-color: transparent;
    border: none;
    background-image: url(../../images/Interface/Settings.svg);
    display: flex;
    min-width: 24px;
    min-height: 24px;
    background-position: center;
    background-repeat: no-repeat;
  }
}